import request from '@/utils/request'

// 登录方法
export function login(username, password, code, uuid) {
  const data = {
    username,
    password,
    code,
    uuid
  }
  return request({
    'url': '/login',
    headers: {
      isToken: false
    },
    'method': 'post',
    'data': data
  })
}

// 微信登录方法
export function wxLogin(code, encryptedIv, encryptedData, nickName, avatar,appId,appSecret) {
  const data = {
    code,
    encryptedIv,
    encryptedData,
	nickName,
	avatar,
	appId,
	appSecret
  }
  return request({
    'url': '/mini/wxLogin',
    headers: {
      isToken: false
    },
    'method': 'post',
    'data': data
  })
}
// 企业登录方法
export function qywxLogin(code,qwcode,appId,appSecret) {
  const data = {
    code,qwcode,appId,appSecret
  }
  return request({
    'url': '/mini/qywxLogin',
    headers: {
      isToken: false
    },
    'method': 'post',
    'data': data
  })
}
// 注册方法
export function register(data) {
  return request({
    url: '/register',
    headers: {
      isToken: false
    },
    method: 'post',
    data: data
  })
}

// 获取用户详细信息
export function getInfo() {
  return request({
    'url': '/getInfo',
    'method': 'get'
  })
}

// 退出方法
export function logout() {
  return request({
    'url': '/logout',
    'method': 'post'
  })
}

// 获取验证码
export function getCodeImg() {
  return request({
    'url': '/captchaImage',
    headers: {
      isToken: false
    },
    method: 'get',
    timeout: 20000
  })
}
